import React, { Component, Fragment } from 'react';
import { Alert } from 'reactstrap';
import {
  boolean as bool_icon, invalid_boolean as bool_invalid_icon,
  color as color_icon, invalid_color as color_invalid_icon,
  custom as rgx_icon, invalid_custom as rgx_invalid_icon,
  float as float_icon, invalid_float as float_invalid_icon,
  integer as int_icon, invalid_integer as int_invalid_icon,
  string as str_icon, invalid_string as str_invalid_icon,
  inputlist as float_il_icon, invalid_inputlist as float_il_invalid_icon,
  inputlist as int_il_icon, invalid_inputlist as int_il_invalid_icon,
  inputlist as rgx_il_icon, invalid_inputlist as rgx_il_invalid_icon,
  inputlist as str_il_icon, invalid_inputlist as str_il_invalid_icon,
  paragraph as p_icon, invalid_paragraph as p_invalid_icon,
  section as sec_icon, invalid_section as sec_invalid_icon,
  section as pack_icon, invalid_section as pack_invalid_icon,
  error as errorsIcon,
  errorAlert as errorsAlertIcon,
  alert as alertIcon,
  warnings as warningsIcon,
  warningAlert as warningsAlertIcon,
  fatal_error,
  up, down, view, unview, dropIco as drop,
  preview, designer, exportIco,
  copy, close as closeIcon, copy_success, copy_failed,
  codePreprocess
} from './preprocessing';
import Icon from '@mdi/react';
import { mdiInformation } from '@mdi/js';



const icons = {
  general: {
    close: closeIcon,
    copy: copy,
    export: exportIco,
    copy_success: copy_success,
    designer: designer,
    preview: preview,
    copy_failed: copy_failed,
    alert: alertIcon
  },
  container: {
    view: view,
    unview: unview,
    drop: drop,
    arrowDown: down,
    arrowUp: up
  },
  types: {
    bool: {
      icon: bool_icon,
      invalid_icon: bool_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon
    },
    color: {
      icon: color_icon,
      invalid_icon: color_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon,
      warnings: warningsIcon,
      warningsAlert: warningsAlertIcon
    },
    rgx: {
      icon: rgx_icon,
      invalid_icon: rgx_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon,
      warnings: warningsIcon,
      warningsAlert: warningsAlertIcon
    },
    float: {
      icon: float_icon,
      invalid_icon: float_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon,
      warnings: warningsIcon,
      warningsAlert: warningsAlertIcon
    },
    int: {
      icon: int_icon,
      invalid_icon: int_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon,
      warnings: warningsIcon,
      warningsAlert: warningsAlertIcon
    },
    p: {
      icon: p_icon,
      invalid_icon: p_invalid_icon,
      alert: alertIcon
    },
    str: {
      icon: str_icon,
      invalid_icon: str_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon,
      warnings: warningsIcon,
      warningsAlert: warningsAlertIcon
    },
    float_il: {
      icon: float_il_icon,
      invalid_icon: float_il_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon,
      warnings: warningsIcon,
      warningsAlert: warningsAlertIcon
    },
    int_il: {
      icon: int_il_icon,
      invalid_icon: int_il_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon,
      warnings: warningsIcon,
      warningsAlert: warningsAlertIcon
    },
    rgx_il: {
      icon: rgx_il_icon,
      invalid_icon: rgx_il_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon,
      warnings: warningsIcon,
      warningsAlert: warningsAlertIcon
    },
    str_il: {
      icon: str_il_icon,
      invalid_icon: str_il_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon,
      warnings: warningsIcon,
      warningsAlert: warningsAlertIcon
    },
    sec: {
      icon: sec_icon,
      invalid_icon: sec_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon
    },
    pack: {
      icon: pack_icon,
      invalid_icon: pack_invalid_icon,
      errors: errorsIcon,
      errorsAlert: errorsAlertIcon
    }
  }
}

const generatorProps = `icons: PropTypes.shape({
    close: PropTypes.any.isRequired,
    copy: PropTypes.any.isRequired,
    designer: PropTypes.any.isRequired,
    preview: PropTypes.any.isRequired,
    copy_success: PropTypes.any.isRequired,
    export: PropTypes.any.isRequired,
    copy_failed: PropTypes.any.isRequired,
    container: PropTypes.exact({
      view: PropTypes.any.isRequired,
      unview: PropTypes.any.isRequired,
      drop: PropTypes.any.isRequired,
      arrowDown: PropTypes.any.isRequired,
      arrowUp: PropTypes.any.isRequired
    }),
    types: PropTypes.exact({
      <comment>// primitives</comment> 
      bool: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired
      }).isRequired,
      color: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired,
        warnings: PropTypes.any.isRequired,
        warningsAlert: PropTypes.any.isRequired
      }).isRequired,
      rgx: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired
      }).isRequired,
      float: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired,
        warnings: PropTypes.any.isRequired,
        warningsAlert: PropTypes.any.isRequired
      }).isRequired,
      int: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired,
        warnings: PropTypes.any.isRequired,
        warningsAlert: PropTypes.any.isRequired
      }).isRequired,
      p: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        alert: PropTypes.any.isRequired
      }).isRequired,
      str: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired,
        warnings: PropTypes.any.isRequired,
        warningsAlert: PropTypes.any.isRequired
      }).isRequired,

      <comment>// input lists</comment>   
      float_il: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired,
        warnings: PropTypes.any.isRequired,
        warningsAlert: PropTypes.any.isRequired
      }).isRequired,
      int_il: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired,
        warnings: PropTypes.any.isRequired,
        warningsAlert: PropTypes.any.isRequired
      }).isRequired,
      rgx_il: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired,
        warnings: PropTypes.any.isRequired,
        warningsAlert: PropTypes.any.isRequired
      }).isRequired,
      str_il: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired,
        warnings: PropTypes.any.isRequired,
        warningsAlert: PropTypes.any.isRequired
      }).isRequired,

      <comment>// sections</comment> 
      sec: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired
      }).isRequired,
      pack: PropTypes.shape({
        icon: PropTypes.any.isRequired,
        invalid_icon: PropTypes.any.isRequired,
        errors: PropTypes.any.isRequired,
        errorsAlert: PropTypes.any.isRequired
      }).isRequired
    })
  })`;

export default class Icons extends Component {
  constructor(props) {
    super(props);
    this.state = {
      json: [
        {
          "uid": "unic_old",
          "name": "How old is your unicorn?",
          "required": true,
          "type": "int"
        },
        {
          "uid": "unic_color",
          "name": "What's the best color of unicorn?",
          "tip": "Try to use this tip: https://youtu.be/5phx8eycHZ4",
          "type": "color"
        }
      ]
    };
  }

  render() {
    return (
      <Fragment>
        <section>
          <h2>
            Designer
          </h2>
        </section>

        <section id="properties">
          <h3>
            Icons
          </h3>
          <div className="pb-3">
            Some of these icons are from library: <a href="https://www.npmjs.com/package/@mdi/js">@mdi/js</a>, but there are also icons which
            were tailored by me just for this library.
          </div>
          <Alert color="info">
            <b><Icon path={mdiInformation} size={0.9} color="#26c6da" className="mr-1 mb-1" /> Note</b><br />
            <div style={{ color: "#777777" }}>
              Icons with name <b>invalid_icon</b> are shown only when element from designer is not valid.
            </div>
          </Alert>
          <div className="mb-4">
            {Object.keys(icons).map((item, key) => {
              var subicons = icons[item];
              var sub = Object.keys(subicons);
              return (
                <div className="pt-2 pb-2" key={key}>
                  <b>{item}</b>
                  <div className="lvl-1-icon-set pt-2">
                    {
                      (item !== 'types')
                        ? <Fragment>
                          {
                            sub.map((subitem, subkey) => {
                              return <div key={subkey} className="ico">
                                <div className="ico-body">{subicons[subitem]}</div>
                                <div className="ico-title">{subitem}</div>
                              </div>;
                            })
                          }
                        </Fragment>
                        : <div>
                          {sub.map((subitem, subkey) => {
                            var subsubicons = subicons[subitem];
                            var subsub = Object.keys(subsubicons);
                            return (
                              <div className="pt-2 pb-2 lvl-2-icon-set" key={key}>
                                <b>{subitem}</b>
                                <div className="lvl-1-icon-set pt-2">
                                  {
                                    subsub.map((subitem, subkey) => {
                                      return <div className="ico">
                                        <div className="ico-body">{subsubicons[subitem]}</div>
                                        <div className="ico-title">{subitem}</div>
                                      </div>;
                                    })
                                  }
                                </div>
                              </div>
                            );
                          }
                          )}
                        </div>
                    }
                  </div>
                </div>
              );
            }
            )}
          </div>

          <h3>
            Property definition
                    </h3>
          <playground>
            <code className="norm-border-radius" dangerouslySetInnerHTML={{ __html: codePreprocess(generatorProps) }}>
            </code>
          </playground>
        </section>
      </Fragment>
    );

  }
}